<template>
	<view class="foot">
		<view class="item" v-for="(item,index) in list" :key="index" :class="{active:page==index}" @click="goPage(index)">
			 <view class="pic">
				  <image class="img1" :src="item.img" mode=""></image>
				  	  <image class="img2" :src="item.img2" mode=""></image>
			 </view>
			 <view class="title">
				 {{item.title}}
			 </view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				list:[
					{
						img:require('../static/images/home.png'),
						img2:require('../static/images/home_selected.png'),
						title:'首页'
					},
					{
						img:require('../static/images/a.png'),
						img2:require('../static/images/a_selectd.png'),
						title:'关于我们'
					},
					{
						img:require('../static/images/solution.png'),
						img2:require('../static/images/solution_selectd.png'),
						title:'解决方案'
					},
					{
						img:require('../static/images/cases.png'),
						img2:require('../static/images/cases_selected.png'),
						title:'客户案例'
					}
				]
			};
		},
		props:['page'],
		methods:{
			goPage(n){
				if(n==0){
					uni.navigateTo({
						url:'../pages/index/index'
					})
				}else if(n==1){
					uni.navigateTo({
						url:'../pages/index/index'
					})
				}else if(n==2){
					uni.navigateTo({
						url:'../pages/index/index'
					})
				}else if(n==3){
					console.log("aa")
					uni.navigateTo({
						url:'../pages/cases/index'
					})
				}
			}
		}
	}
</script>

<style lang="scss">
.foot{
	width:100%;
	background-color: white;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	position: fixed;
	bottom:0;
	left:0;
	padding: 20rpx 0;
	.item{
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		text-align: center;
		width:23%;
		.pic{
			position: relative;
			display: inline-block;
			image{
				display: block;
				max-width: 50rpx;
				max-height: 50rpx;
			}
			.img2{
				position: absolute;
				left:0;
				top:0;
				object-fit: contain;
				width:100%;
				height:100%;
				opacity: 0;
			}
		}
		.title{
			color:#949494;
			margin: 15rpx auto;
			font-size: 26rpx;
		}
	}
	.active{
		.pic{
			.img1{
				opacity: 0;
			}
			.img2{
				opacity: 1;
			}
		}
		.title{
			color:#333333;
		}
	}
}
</style>
